html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  width: 100vw;
  height: 100vh;
  overflow-x: hidden;
  overflow-x-overflow-y: hidden;
}

.main {
  width: 100%;
  background: #ddd;
  padding-top: 150px;
}

.fake-section {
  margin: 0 auto;
  max-width: 1200px;
  width: 100%;
  background: #fff;
}

.canvas-container {
  position: relative;
  width: 100%;
  padding-top: 56.25%;
}

canvas {
  display: inline-block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.loader {
  position: absolute;
  z-index: 3;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  overflow: hidden;
}

.loader__top-half {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: calc(50% - 2px);
  border-bottom: 2px solid transparent;
  background: #000;
}

.loader__bottom-half {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: calc(50% - 2px);
  background: #000;
  border-top: 2px solid transparent;
}

.loader__progress-bar {
  position: absolute;
  top: 50%;
  right: 0;
  width: 100%;
  transform: translateY(-50%);
  height: 4px;
  background: #000;
  transition: width 0.1s ease;
}

.glimat-logo {
  position: absolute;
  z-index: 2;
  bottom: 0;
  left: 0;
  width: 20%;
  min-width: 60px;
  max-width: 250px;
  padding-left: 20px;
  padding-bottom: 20px;
  pointer-events: none;
}
